import HTTP from '@/httpapi';
import Paginate from '@lib/paginate';
import { cacheBasic, cacheReferentiel } from '@lib/cache';

const User = {
    myAccount (forceUpdate) {
        forceUpdate = forceUpdate || false;
        return HTTP.http.get(HTTP.convert('/v1/user/me'), {cache: cacheReferentiel, forceUpdate: forceUpdate});
    },
    details (userId, ...extras) {
        if (extras.length === 0) {
            extras = ['achievements', 'userapps', 'nbbuddies', 'onlinegames'];
        }

        return HTTP.http.get(HTTP.convert('/v1/user/' + userId + '?extras=' + extras.join(','), 'details'), {cache: cacheReferentiel});
    },
    achievements (userId) {
        userId = userId || 'me';
        return HTTP.http.get(HTTP.convert('/v1/user/' + userId + '?extras=achievements', 'achievements'), {cache: cacheReferentiel});
    },
    activities (userId) {
        return HTTP.http.get(HTTP.convert('/v1/user/' + userId + '?extras=userapps', 'activity'), {cache: cacheBasic});
    },
    onlineGames (userId, gameId) {
        return HTTP.http.get(HTTP.convert('/v1/user/' + userId + '/games/summary' + (gameId ? '/' + gameId : '')), {cache: cacheBasic});
    },
    rank (userId, gameId) {
        return HTTP.http.get(HTTP.convert('/v1/user/' + userId + '/rank/' + gameId), {cache: cacheBasic});
    },
    myRank (gameId) {
        return User.rank('me', gameId);
    },
    buddiesCollection () {
        return HTTP.http.get('/v1/user/me/buddies/collection', {cache: cacheReferentiel});
    },
    addBuddy (userId) {
        return HTTP.http.post('/v1/user/me/buddies/' + userId);
    },
    removeBuddy (userId) {
        return HTTP.http.delete('/v1/user/me/buddies/' + userId);
    },
    // last 15 days
    lastgames (userId, gameId, status, page, limit) {
        return Paginate(HTTP.convert('/v1/user/' + userId + '/lastgames/' + gameId, 'lastgames'), page, { limit: limit, status: status }, { cache: cacheReferentiel });
    },
    // all by year, by default current year (without last 15 days) range from 2002 to now
    history (userId, gameId, status, period, page, limit) {
        return Paginate(HTTP.convert('/v1/user/' + userId + '/games/' + gameId, 'history'), page, { limit: limit, y: period, status: status, o: 0 }, { cache: cacheReferentiel });
    },
};

export default User;



