import HTTP from '@/httpapi';
import { cacheReferentiel } from '@lib/cache';

const Games = {
    all () {
        return HTTP.http.get(HTTP.convert('/v1/games'), {cache: cacheReferentiel});
    },
    rankables () {
        return HTTP.http.get(HTTP.convert('/v1/game/rankables'), {cache: cacheReferentiel});
    },
    achievements (gameId) {
        return HTTP.http.get(HTTP.convert('/v1/game/' + gameId + '/achievements?limit=200'), {cache: cacheReferentiel});
    },
    crosspromo (gameId, lang) {
        return HTTP.http.get(HTTP.convert('/v1/game/' + gameId + '/product/' + lang), {cache: cacheReferentiel});
    },
};

export default Games;



