<template>
    <keep-alive>
        <panel :config="config" class="achievements" v-if="gameAchievements.length > 0">
            <template slot="header">&nbsp;<span v-if="gameName" v-html="getTrans('achievements_for', {'%game%': gameName})"></span></template>
            <template slot="body">
                <h4 v-if="userAchievements.length > 0">{{ getTrans('owned_achievements') }}</h4>
                <div v-if="userAchievements.length > 0" class="achievements_list owned">
                    <div v-for="ach in userAchievements" :key="ach.id" class="achievement_item" :style="{order: ach.id}" v-if="!ach.isAlwaysHidden() && getAchievementDescription(ach)">
                        <div class="icon-container"><img v-if="ach.hasPictureOrRibbon()" :src="forceHTTPS(getAchievementIcon(ach))" class="achievement icon" :class="getAchievementIconClass(ach, )" @error="achievementError" /></div>
                        <div v-html="getAchievementDescription(ach, (['xxs', 'xs', 'sm'].includes($mq) ? $moment.unix(ach.awarded_ts).format('LL') : $moment.unix(ach.awarded_ts).format('LLLL')))"></div>
                    </div>
                </div>
                <hr v-if="userAchievements.length > 0 && gameAchievements.length > 0" />
                <h4 v-if="isLogged && gameAchievements.length > 0">{{ getTrans('locked_achievements') }}</h4>
                <div v-if="gameAchievements.length > 0" class="achievements_list">
                    <div v-for="ach in gameAchievements" :key="ach.id" class="achievement_item unowned" :style="{order: ach.id}">
                        <div class="icon-container"><img :src="forceHTTPS(getAchievementIcon(ach))" class="achievement icon" :class="getAchievementIconClass(ach)" @error="achievementError" /></div>
                        <div v-html="getAchievementDescription(ach)"></div>
                    </div>
                    <div v-if="secret > 0" class="achievement_item hidden_counts" :style="{order: upperOrder}">
                        <h4>{{ getTransChoice(secret, 'and_hidden_counts', {'%count%': secret}) }}</h4>
                    </div>
                </div>
            </template>
        </panel>
    </keep-alive>
</template>

<script>
import axios from 'axios';
import HTTP from '@/httpapi';
import Games from '@api/games';
import User from '@api/user';
import GenericMixin from '@mixins/generic';
import AchievementMixin from '@mixins/achievements';
import Achievement from '@classes/achievement';

export default {
    name: 'game-achievements',

    props: ['config'],

    mixins: [GenericMixin, AchievementMixin],

    computed: {
        gameId () {
            return this.config.game.id;
        },

        gameName () {
            return this.config.game.name;
        },

        gameAchievements () {
            let achs = [];
            let ids = this.userAchievements.map((el) => parseInt(el.id));
            if (this.achievements) {
                let that = this;
                achs = this.achievements.filter(function (el) {
                    if (parseInt(el.id) > that.upperOrder) {
                        that.upperOrder = parseInt(el.id) + 10;
                    }
                    if (ids.length === 0 || ids.indexOf(parseInt(el.id)) === -1) {
                        if (!el.retired && !el.isAlwaysHidden() && that.getAchievementDescription(el) && !el.isEvent()) {
                            if (el.secret) {
                                that.secret += 1;
                            } else {
                                return true;
                            }
                        }
                    }

                    return false;
                });
            }

            return achs;
        },

        userAchievements () {
            if (this.user && this.user.achievements[this.gameId]) {
                return this.user.achievements[this.gameId].map((el) => new Achievement(el));
            }

            return [];
        }
    },

    methods: {
        getUserAchievements () {
            if (this.isLogged) {
                return User.achievements();
            }

            return null;
        },

        getAchievements () {
            let that = this;
            axios.all([
                Games.achievements(that.gameId),
                that.getUserAchievements()
            ]).then(axios.spread(function (achievements, user) {
                that.achievements = achievements.data.achievements.map((el) => new Achievement(el));
                if (user) {
                    that.user = user.data.user;
                }
            }));
        }
    },

    mounted () {
        HTTP.ifInitialized(this.getAchievements);
    },

    data () {
        return {
            achievements: null,
            user: null,
            secret: 0,
            upperOrder: 0
        };
    }
};
</script>

<style scoped lang="scss">
@import '~sass/_variables.scss';
@import '~sass/image.scss';

.panel.achievements /deep/ {
    h4 {
        color: map-get($asnet-colors, dark-purple);
        font-size: 1em;
        max-height: 140px;
        margin-top: 0;
        margin: auto;
    }
    .achievements_list {
        display: flex;
        flex-wrap: wrap;
        justify-content: flex-start;

        .achievement_item {
            border: solid 1px $border-gray;
            border-radius: 5px;
            display: flex;
            font-size: .8em;
            margin: 5px;
            padding: 5px;
            white-space: nowrap;
            width: 268px;

            &.unowned {
                filter: grayscale(100%);
            }

            &.hidden_counts {
                height: 80px;
            }

            .icon-container {
                align-items: center;
                display: flex;
                height: $icon-max-height;
                justify-content: center;
                margin: auto;
                margin-left: 5px;
                margin-right: 5px;
                width: $icon-max-width;
            }
            img {
                margin: 0;
            }

            div {
                display: inline-block;
                margin: auto 3px auto 3px;
                white-space: normal;
                p {
                    display: block;
                }
            }
        }
    }
}

@include media-breakpoint-down(sm) {
    .panel.achievements {
        .achievements_list {
            .achievement_item {
                font-size: .8em;
                width: 255px;

                h4 {
                    font-size: .8em;
                }
            }
        }
    }
}

@include media-breakpoint-up(xl) {
    .panel.achievements {
        .achievements_list {
            .achievement_item {
                font-size: .8em;
                width: 330px;
            }
        }
    }
}

@include media-breakpoint-between(sm, md) {
    .panel.achievements {
        .achievements_list {
            .achievement_item {
                font-size: .8em;
                width: 258px;
            }
        }
    }
}
</style>



