import store from '@/store';
import ImageError from '@/lib/ImageError';
import Trans from '@/lib/Trans';

const hybridExtLinks = {
    keyforge: 'https://www.keyforgegame.com/',
    xwing: 'https://squadbuilder.fantasyflightgames.com/'
};

const genericMixin = {
    props: ['config'],

    computed: {
        title () {
            return this.getTrans(this.$options.name + '_title');
        },

        lang () {
            if (this.$moment.locale() !== store.getters.globalOptions.lang) {
                this.$moment.locale(store.getters.globalOptions.lang);
            }

            return store.getters.globalOptions.lang;
        },

        gameIconError () {
            return ImageError.game;
        },

        avatarError () {
            return ImageError.avatar;
        },

        achievementError () {
            return ImageError.achievement;
        },

        genericImageError () {
            return ImageError.generic;
        },

        isLogged () {
            return !store.getters.unloggedMode;
        },

        isIn3rdParty () {
            return !!store.getters.globalOptions.in_3rd_party;
        },

        baseHost () {
            return (store.getters.globalOptions.host ? store.getters.globalOptions.host.replace(/\/$/, '') : '');
        },

        baseISImageHost () {
            switch (process.env.NODE_ENV) {
            case 'production':
                return 'https://ncdn.asmodee.net/is/img/';

            default:
                return this.baseHost + '/img/';
            }
        },
    },

    methods: {
        getTrans (entry, substitutions) {
            return Trans.getTrans(this.config.trans, this.lang, entry, substitutions);
        },

        getTransChoice (enumerable, entry, substitutions) {
            return Trans.getTransChoice(this.config.trans, this.lang, enumerable, entry, substitutions);
        },

        urlFor (name, params) {
            params = params || {};
            if (this.config.urls) {
                if (this.config.urls[name]) {
                    let burl = this.config.urls[name];
                    Object.keys(params).forEach(function (k) {
                        burl = burl.replace(':' + k, params[k]);
                    });

                    if (!/^\/[a-z]{2}\/.*/.exec(burl)) {
                        burl = '/' + this.lang + burl;
                    }

                    return this.baseHost + burl;
                }
            }

            return '';
        },

        gameUrl (gameId) {
            if (['keyforge', 'xwing'].includes(gameId)) {
                return hybridExtLinks[gameId];
            } else {
                return this.urlFor('game-index', {'id': gameId});
            }
        },

        goToGame (gameId) {
            let url = this.gameUrl(gameId);
            if (['keyforge', 'xwing'].includes(gameId)) {
                window.open(url);
            } else {
                if (this.isIn3rdParty) {
                    window.open(url);
                } else {
                    window.location = url;
                }
            }

            return false;
        },

        goToGameRanking (gameId) {
            let url = this.urlFor('rankings-game', {'id': gameId});
            if (this.isIn3rdParty) {
                window.open(url);
            } else {
                window.location = url;
            }
        },

        forceHTTPS (url) {
            if (!url) {
                return url;
            }

            return url.replace('^http://', 'https://');
        }
    }
};

export default genericMixin;



